import Api from '@/common/api';
import share from '@/components/share';
import { mapState } from 'vuex';
export default {
	components: {
		share
	},
	data() {
		return {
			specClass: 'none',
			specSelected: [],
			small: null,
			sku: [],
			detailData: [],
			favorite: true,
			shareList: [],
			cat_list: [],

			desc: `
					<div style="width:100%">
						<img style="width:100%;display:block;" src="https://gd3.alicdn.com/imgextra/i4/479184430/O1CN01nCpuLc1iaz4bcSN17_!!479184430.jpg_400x400.jpg" />
						<img style="width:100%;display:block;" src="https://gd2.alicdn.com/imgextra/i2/479184430/O1CN01gwbN931iaz4TzqzmG_!!479184430.jpg_400x400.jpg" />
						<img style="width:100%;display:block;" src="https://gd3.alicdn.com/imgextra/i3/479184430/O1CN018wVjQh1iaz4aupv1A_!!479184430.jpg_400x400.jpg" />
						<img style="width:100%;display:block;" src="https://gd4.alicdn.com/imgextra/i4/479184430/O1CN01tWg4Us1iaz4auqelt_!!479184430.jpg_400x400.jpg" />
						<img style="width:100%;display:block;" src="https://gd1.alicdn.com/imgextra/i1/479184430/O1CN01Tnm1rU1iaz4aVKcwP_!!479184430.jpg_400x400.jpg" />
					</div>
				`,
			skuList: []
		};
	},
	async onLoad(ops) {
		//接收传值,id里面放的是标题，因为测试数据并没写id
		let id = ops.id;
		if (id) {
			this.logining = true;
			let params = { id: ops.id };
			let data = await Api.apiCall('get', Api.goods.storeDetail, params);
			this.logining = false;

			if (data.data) {
				let detailData = data.data.store;
				this.detailData = detailData;
				this.cat_list = data.data.store.list;
				this.favorite = data.data.favorite;
				this.loaded = true;

				uni.setNavigationBarTitle({
					title: detailData.name
				});
			}
		}

		this.shareList = await this.$api.json('shareList');
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			});
		},
		//分享
		share() {
			this.$refs.share.toggleMask();
		},
		//收藏
		toFavorite(item) {
			if (!this.hasLogin) {
				let url = '/pages/public/login';
				uni.navigateTo({
					url
				});
			} else {
				this.favorite = !this.favorite;
				let params = { objId: item.id, type: 3, memberId: this.userInfo.userInfo.id, name: item.name, meno1: item.logo, meno2: item.hit, meno3: item.collect };
				Api.apiCall('post', Api.goods.favoriteSave, params);
			}
		},

		async jifenPay(item) {
			if (!this.hasLogin) {
				let url = '/pages/public/login';
				uni.navigateTo({
					url
				});
				return false;
			}

			let id = item.id;
			let data;

			let params = { goodsId: id, memberId: this.userInfo.userInfo.id };
			data = await Api.apiCall('post', Api.order.jifenPay, params);

			if (data) {
				this.$api.msg(data);
			}
		},
		stopPrevent() {}
	}
};
